#nav_bar {
  position: relative;
  overflow: hidden;
  z-index: var(--z-index-navbar);
  margin: 0 auto;
  font-size: 16px;
  line-height: 1.5;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10vw;
  padding-right: 10vw;
  border-bottom: 1px solid rgba(63, 70, 89, 0.22);
  background-color: #ffffff;
  transition: top .3s ease-out; }

#ol_logo {
  float: left;
  margin-top: 6px;
  margin-right: 30px;
  width: 37px;
  height: 27px; }

.nav_list {
  float: left;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  z-index: 99999; }
  .nav_list li {
    float: left; }
    .nav_list li a {
      display: inline-block;
      color: #5E6B8D;
      text-align: center;
      padding: 8px 25px; }
      .nav_list li a:hover {
        color: #24253A;
        opacity: 1; }

#header_round_links_container {
  display: none; }

#hamburger_toggle_button {
  display: none; }

.qa-descriptor {
  position: absolute;
  top: 10px;
  right: 10px;
  color: blue;
  background-color: orange;
  padding: 9px 14px;
  z-index: 999999;
  font-size: 14px; }

@media (max-width: 991.98px) {
